const Home = [
    {
        path: '/',
        //name: 'home',
        redirect: '/home',
        component: Main,
        meta: {
            hideInMenu: true,
            notCache: true,
            requireAuth: true
        },
        children: [{
            path: '/home',
            name: 'home',
            // component: () => import('@/views/Home/home.vue'),
            component: (resolve) => require(['@/views/Home/home.vue'], resolve),
            meta: {
                hideInMenu: true,
                title: '',
                notCache: true,
                requireAuth: true
            }
        }]
    }
];


