/**
 * axios
 * @return axios
 * */

import axios from 'axios';
import utils from './utils';
import Config from '@/config';
import iview from 'iview';
import router from '../../router';

const ajaxUrl = Config[Config.env].origin + Config[Config.env].ajaxUrl;

function setContentTypeIfUnset(headers, value) {
    if (!utils.isUndefined(headers) && utils.isUndefined(headers['Content-Type'])) {
        headers['Content-Type'] = value;
    }
}

axios.defaults.withCredentials=true;

const Ajax = axios.create({
    baseURL: ajaxUrl,
    timeout: 30000,
    transformRequest: [function transformRequest(data, headers) {
        /* content-typeContent-Type */
        let keys = Object.keys(headers);
        let normalizedName = 'Content-Type';
        keys.forEach(name => {
            if (name !== normalizedName && name.toUpperCase() === normalizedName.toUpperCase()) {
                headers[normalizedName] = headers[name];
                delete headers[name];
            }
        });
        if (utils.isFormData(data) ||
            utils.isArrayBuffer(data) ||
            utils.isStream(data) ||
            utils.isFile(data) ||
            utils.isBlob(data)
        ) {
            return data;
        }
        if (utils.isURLSearchParams(data)) {
            setContentTypeIfUnset(headers, 'application/x-www-form-urlencoded;charset=utf-8');
            return data.toString();
        }
        /* axios */
        /* POSTjsonjsonrequest payload
         *  {name: 'admin', pwd: 123}name=admin&pwd=123
         * x-www-form-urlencoded
         * */
        if (utils.isObject(data)) {
            setContentTypeIfUnset(headers, 'application/x-www-form-urlencoded;charset=utf-8');
            let keys2 = Object.keys(data);
            /* jsonurlencode */
            return encodeURI(keys2.map(name => `${name}=${data[name]}`).join('&'));
        }
        return data;
    }]
});


//ajax
Ajax.interceptors.request.use(function (config) {
    if (config.method === 'post' && utils.isUndefined(config.headers['Content-Type'])) {
        config.headers['Content-Type'] = 'application/json;charset=utf-8';
    }
    iview.LoadingBar.start();
    return config;
}, function (error) {
    return Promise.reject(error);
});
//ajax
Ajax.interceptors.response.use(function (response) {
    // if (response.data.errCode === 'A0002'){ }

    // let router = new VueRouter();

    switch (response.data.code) {
        case 'SUCCESS':
            iview.LoadingBar.finish();
            break;
        case 'E0008':
            router.push('/login');
            break;
        default:
            iview.LoadingBar.error();
            iview.Message.error(response.data.msg);
            break;
    }
    return response.data;
}, function (error) {
    return Promise.reject(error);
});

export default Ajax;